.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn {
  margin: 2px;
}

.highlight-question {
  font-weight: bold;
  color: #007bff; /* Blue */
}

.highlight-answer {
  font-weight: bold;
  color: #28a745; /* Green */
}

.form-group {
  margin-bottom: 1em;
}

.form-control {
  width: 100%;
  padding: 0.5em;
  margin-top: 0.5em;
}

.form-actions {
  display: flex;
  gap: 1em;
}